

.lessons-swap {
  opacity: 1;

  &__list {
    width: 100%;
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;
    // padding-left: 20%;
    // padding-right: 20%;
    column-gap: 5%;
  }

  &__item {
    background: var(--gr-1);
    border: 2px solid  var(--gr-4);
    margin-bottom: 10px;
    padding: 20px;
    border-radius: 25px;
    border: 5px solid var(--white);
    color: white;
    text-shadow: 1px 1px 0 var(--black);
    transition: 300ms ease;

    &:hover {
      border: 5px solid var(--gr-2);
      transition: 300ms ease;
    }


  }

  &__link {

    @media @desktop {
      font-size: 60px;
    }
  }
}
