@font-face {
  font-family: "Roboto";
  font-display: swap;
  src: url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto-Regular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
}
@font-face {
  font-family: "Roboto";
  font-display: swap;
  src: url("../fonts/Roboto-Medium.woff2") format("woff2"), url("../fonts/Roboto-Medium.woff") format("woff");
  font-style: normal;
  font-weight: 500;
}
@font-face {
  font-family: "Roboto";
  font-display: swap;
  src: url("../fonts/Roboto-Italic.woff2") format("woff2"), url("../fonts/Roboto-Italic.woff") format("woff");
  font-style: italic;
  font-weight: 400;
}
:root {
  --basic-font: "Roboto", "Arial", sans-serif;
  --black: #212121;
  --white: #ffffff;
  --sp-white: rgba(0, 40, 120, 0);
  --gr-1: #0f945a;
  --gr-2: #36c598;
  --gr-3: #27d883;
  --gr-4: #21b76f;
  --gr-5: rgba(18, 167, 105, 0.75);
  --blue: #2692ff;
  --grey: #444444;
  --grey-2: rgba(123, 123, 123, 0.2);
}
.visually-hidden {
  transform: scale(0);
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}
a {
  text-decoration: none;
  color: inherit;
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}
*,
*:hover,
*:focus {
  cursor: default;
}
.page {
  height: 100%;
}
.page-body {
  height: 100%;
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  font-family: var(--basic-font);
  background: var(--grey-2);
  width: 1920px;
  max-width: 1920px;
}
.running-line {
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  position: absolute;
  bottom: 0;
}
.running-line__text {
  color: var(--black);
  font-size: 26px;
  display: inline-block;
  padding-left: 100%;
  animation: running-line 20s infinite linear;
}
@keyframes running-line {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(-100%, 0);
  }
}
.header {
  position: relative;
  margin: 0;
  background: #29bd5b;
  background: linear-gradient(90deg, #29bd5b 20%, #51b91f 40%, #20d03a 60%, #65d261 80%);
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  border-bottom: 13px solid var(--white);
  padding: 10px 25px 0 25px;
  box-sizing: border-box;
}
.header__img {
  position: absolute;
  left: 2%;
  top: 2%;
  width: 135px;
  height: 135px;
}
.header__name {
  display: flex;
  column-gap: 10px;
  color: var(--white);
  text-shadow: 1px 1px 0 var(--black), 1px -1px 0 var(--black), -1px -1px 0 var(--black), -1px 1px 0 var(--black);
  align-self: center;
  margin-left: 6%;
  font-size: 36px;
  margin-top: 30px;
  margin-bottom: 30px;
}
.nav-menu {
  opacity: 1;
  width: 100%;
  padding-left: 150px;
}
.nav-menu__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  width: 100%;
  column-gap: 20px;
}
.nav-menu__item {
  margin: 0;
  text-transform: none;
  font-size: 28px;
  padding: 5px 10px;
  font-weight: 600;
  color: var(--white);
  cursor: pointer;
  min-height: 62px;
  text-align: center;
  border: none;
  background-size: 300% 100%;
  text-shadow: 0 1px 0 #312c2c;
  transition: all 400ms ease-in-out;
  border-radius: 5px;
  background-image: linear-gradient(to right, var(--gr-1), var(--gr-2), var(--gr-3), var(--gr-4));
  display: flex;
  align-items: center;
  justify-content: center;
}
.nav-menu__item:hover {
  box-shadow: 0 0 0 0 var(--sp-white);
  background-position: 100% 0;
  transition: all 400ms ease-in-out;
}
.nav-menu__item:focus {
  outline: none;
}
.nav-menu__label {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  column-gap: 5px;
}
.main {
  background-image: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAACMAAAAjCAMAAAApB0NrAAAABlBMVEX///+pqalTpKl9AAAAAnRSTlMAGovxNEIAAAAdSURBVDjLY2AAAUaG4UYxDDsfjfp91O+jfqfE7wCzpQCw/dq+qQAAAABJRU5ErkJggg==");
}
.main__btn {
  display: none;
}
.main * {
  background-image: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAACMAAAAjCAMAAAApB0NrAAAABlBMVEX///+pqalTpKl9AAAAAnRSTlMAGovxNEIAAAAdSURBVDjLY2AAAUaG4UYxDDsfjfp91O+jfqfE7wCzpQCw/dq+qQAAAABJRU5ErkJggg==");
}
.main-section {
  display: flex;
  justify-content: flex-start;
  padding: 25px;
  height: 100%;
  min-height: 100%;
}
.main .main-section--1,
.main .main-section--2,
.main .main-section--3,
.main .main-section--4 {
  display: none;
}
.main__btn--1:checked ~ .main .main-section--1,
.main__btn--2:checked ~ .main .main-section--2,
.main__btn--3:checked ~ .main .main-section--3,
.main__btn--4:checked ~ .main .main-section--4 {
  display: flex;
  align-items: stretch;
  height: 100%;
}
.main__btn--1:checked ~ .header .nav-menu .nav-menu__list .nav-menu__item--1,
.main__btn--2:checked ~ .header .nav-menu .nav-menu__list .nav-menu__item--2,
.main__btn--3:checked ~ .header .nav-menu .nav-menu__list .nav-menu__item--3,
.main__btn--4:checked ~ .header .nav-menu .nav-menu__list .nav-menu__item--4 {
  margin-bottom: -1px;
}
.main__btn--1:checked ~ .header .nav-menu .nav-menu__list .nav-menu__item--1,
.main__btn--2:checked ~ .header .nav-menu .nav-menu__list .nav-menu__item--2,
.main__btn--3:checked ~ .header .nav-menu .nav-menu__list .nav-menu__item--3,
.main__btn--4:checked ~ .header .nav-menu .nav-menu__list .nav-menu__item--4 {
  color: var(--white);
  background: var(--white);
  box-shadow: none;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  text-shadow: 1px 1px 0 var(--black), 1px -1px 0 var(--black), -1px -1px 0 var(--black), -1px 1px 0 var(--black);
}
.main__column-1::-webkit-scrollbar {
  width: 25px;
  /* ширина scrollbar */
}
.main__column-1::-webkit-scrollbar-track {
  background: var(--gr-3);
  /* цвет дорожки */
  border-radius: 25px;
  /* закругления плашки */
}
.main__column-1::-webkit-scrollbar-thumb {
  background-color: var(--gr-1);
  /* цвет плашки */
  border-radius: 25px;
  /* закругления плашки */
  border: 1px solid var(--gr-5);
  /* padding вокруг плашки */
}
.main__column-1 {
  scrollbar-width: thin;
  /* "auto" или "thin"  */
  scrollbar-color: blue orange;
  scroll-behavior: smooth;
}
.time {
  opacity: 1;
  display: grid;
  grid-column-gap: 5px;
  grid-template-areas: "a a a" "d e f";
  font-size: 45px;
}
@media (min-width: 1440px) {
  .time {
    grid-template-areas: "a a a d e f";
    column-gap: 10px;
  }
}
@media (min-width: 1440px) {
  .time--position {
    grid-column: 2/3;
    grid-row: 3/4;
  }
}
.time__now {
  grid-area: a;
  text-align: center;
  font-weight: bold;
  color: var(--white);
  text-shadow: 1px 1px 0 var(--black), 1px -1px 0 var(--black), -1px -1px 0 var(--black), -1px 1px 0 var(--black);
  letter-spacing: 2px;
}
@media (min-width: 1440px) {
  .time__now {
    margin-right: 15px;
  }
}
.time__hours {
  grid-area: a;
}
.time__hours::after {
  content: ":";
  animation-name: timeIn;
  animation-duration: 2s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}
.time__minutes {
  grid-area: b;
}
.time__minutes::after {
  content: ":";
}
.time__seconds {
  grid-column: c;
}
.time__day {
  grid-area: d;
  color: var(--white);
  font-weight: bold;
  text-shadow: 1px 1px 0 var(--black), 1px -1px 0 var(--black), -1px -1px 0 var(--black), -1px 1px 0 var(--black);
  letter-spacing: 2px;
}
.time__month {
  grid-area: e;
  color: var(--white);
  font-weight: bold;
  text-shadow: 1px 1px 0 var(--black), 1px -1px 0 var(--black), -1px -1px 0 var(--black), -1px 1px 0 var(--black);
  letter-spacing: 2px;
}
.time__year {
  grid-area: f;
  color: var(--white);
  font-weight: bold;
  text-shadow: 1px 1px 0 var(--black), 1px -1px 0 var(--black), -1px -1px 0 var(--black), -1px 1px 0 var(--black);
  letter-spacing: 2px;
}
@keyframes timeIn {
  0% {
    color: transparent;
  }
  50% {
    color: var(--white);
  }
  100% {
    color: transparent;
  }
}
head,
body {
  height: 100%;
  max-height: 100%;
}
.section {
  display: flex;
  justify-content: flex-start;
}
.section--one {
  opacity: 1;
  width: 45%;
  overflow-y: scroll;
  height: 670px;
}
.section--two {
  opacity: 1;
  width: 100%;
}
.lessons {
  opacity: 1;
}
.lessons__item {
  outline: 2px solid black;
  width: 100%;
  background-color: var(--white);
  padding: 15px 5px 5px 25px;
  border-radius: 25px;
  border: 3px solid var(--gr-3);
  box-sizing: border-box;
  display: grid;
  grid-template-areas: "i a a a" "i b b b" "c c d e";
}
.lessons__list {
  margin: 0;
  row-gap: 25px;
  flex-wrap: wrap;
  padding: 25px;
  list-style: none;
  display: flex;
  width: 100%;
}
.lessons__title {
  margin: 0;
  font-size: 36px;
  font-style: normal;
  font-weight: 500;
  grid-area: a;
}
.lessons__time-red {
  font-size: 24px;
  grid-area: b;
}
.lessons__author {
  font-size: 24px;
  font-style: normal;
  font-weight: 500;
  grid-area: c;
}
.lessons__time {
  font-size: 26px;
  font-style: normal;
  font-weight: 500;
  grid-area: e;
}
.lessons__img {
  width: 220px;
  height: 130px;
  grid-area: i;
}
.lessons-swap {
  opacity: 1;
}
.lessons-swap__list {
  width: 100%;
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-wrap: wrap;
  column-gap: 5%;
}
.lessons-swap__item {
  background: var(--gr-1);
  border: 2px solid var(--gr-4);
  margin-bottom: 10px;
  padding: 20px;
  border-radius: 25px;
  border: 5px solid var(--white);
  color: white;
  text-shadow: 1px 1px 0 var(--black);
  transition: 300ms ease;
}
.lessons-swap__item:hover {
  border: 5px solid var(--gr-2);
  transition: 300ms ease;
}
@media (min-width: 1440px) {
  .lessons-swap__link {
    font-size: 60px;
  }
}
.news {
  opacity: 1;
}
.news__list {
  display: flex;
  flex-direction: column;
  list-style: none;
  margin: 0;
  padding: 10px;
  row-gap: 10px;
  scroll-behavior: smooth;
}
.news__item {
  background-color: var(--white);
  border-radius: 25px;
}
.news__link {
  display: flex;
  flex-direction: column;
  padding: 10px;
  position: relative;
  border-radius: 25px;
  border: 2px solid var(--gr-1);
}
.news__title {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 36px;
  text-align: center;
}
.news__image {
  width: 100px;
  height: 155px;
}
.news__author {
  font-size: 24px;
  align-self: flex-end;
}
.news__time {
  font-size: 24px;
  align-self: flex-end;
}
.slides,
.slides2 {
  font-size: 32px;
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  box-sizing: border-box;
}
.slide2,
.slide {
  position: absolute;
  opacity: 0;
  padding: 0;
  margin: 0;
  width: 100%;
  min-height: 100%;
  height: 770px;
}
.first,
.first2 {
  min-height: 100%;
  opacity: 1;
  transition: 300ms ease;
  background-color: transparent;
  position: relative;
}
@keyframes scale {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
  100% {
    transform: scale(1);
  }
}
.showing2,
.showing {
  opacity: 1;
  position: relative;
  transition: 1500ms ease;
}
.slide2,
.slide {
  transition: 1500ms ease;
  background-repeat: no-repeat;
  background-position: 50% 50%;
}
.slide-1 {
  background-color: yellow;
}
.slide-2 {
  background-color: green;
}
.slide-3 {
  background-color: tomato;
}
.slide-4 {
  background-color: darkorange;
}
.slider-5 {
  background-color: blue;
}
.swap {
  opacity: 1;
}
.swap__title {
  margin: 0;
  padding: 0;
  font-style: italic;
  font-weight: 400;
  text-align: center;
}
.swap__text {
  margin: 0;
  padding: 0;
  font-weight: 600;
  text-align: center;
}
.swap-table {
  border-collapse: collapse;
  font-size: 26px;
}
.swap-table__cell {
  width: 2%;
  border: 3px solid var(--black);
  text-align: center;
  padding: 5px;
}
.swap__row--1 .swap-table__cell {
  font-weight: 500;
  border: 3px solid var(--black);
}
.swap-table__cell--1 {
  font-weight: 500;
  border: 3px solid var(--black);
}
.swap .main {
  box-sizing: border-box;
  padding: 25px;
}
.swap__ps {
  font-size: 24px;
  font-weight: 500;
  text-align: center;
}
.swap .nav-menu {
  width: auto;
  order: -1;
  padding-left: 0;
}
.swap .time {
  margin-left: 350px;
}
.swap .nav-menu {
  display: flex;
}
.swap .nav-menu__list {
  justify-content: flex-start;
}
.swap .header {
  padding: 0;
  justify-content: flex-start;
  padding: 15px;
}
.swap .header__img {
  display: none;
  position: relative;
}
.smena {
  position: absolute;
  height: 90%;
  border: 5px solid var(--gr-1);
  width: 95%;
  margin: 50px;
  background-color: var(--white);
  box-sizing: border-box;
  padding-top: 10px;
  background-image: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAACMAAAAjCAMAAAApB0NrAAAABlBMVEX///+pqalTpKl9AAAAAnRSTlMAGovxNEIAAAAdSURBVDjLY2AAAUaG4UYxDDsfjfp91O+jfqfE7wCzpQCw/dq+qQAAAABJRU5ErkJggg==");
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: space-around;
  align-content: space-around;
}
.smena__list {
  width: 100%;
  list-style: none;
  padding: 0;
  margin: 0;
  font-size: 32px;
  display: flex;
  column-gap: 10px;
  flex-wrap: wrap;
  justify-content: space-around;
  margin-bottom: 10px;
  order: 2;
}
.smena__label {
  margin: 0;
  padding: 15px;
  border: 5px solid var(--gr-4);
  border-radius: 15px;
  display: flex;
  justify-content: center;
  align-items: stretch;
  transition: 500ms ease;
}
.smena__btn {
  display: none;
}
.smena__lessons {
  list-style: none;
  margin: 0;
  padding: 0;
}
.smena__item {
  box-sizing: border-box;
  margin: 0;
  padding: 10px 5px;
  display: flex;
  flex-direction: column;
}
.smena__content {
  outline: 2px solid var(--black);
  outline-offset: -3px;
}
.smena--one,
.smena--two {
  animation: modal-appearance 1.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}
.smena__btn--1:checked ~ .smena__list .smena__label--1,
.smena__btn--2:checked ~ .smena__list .smena__label--2,
.smena__btn--3:checked ~ .smena__list .smena__label--3,
.smena__btn--4:checked ~ .smena__list .smena__label--4,
.smena__btn--5:checked ~ .smena__list .smena__label--5,
.smena__btn--6:checked ~ .smena__list .smena__label--6,
.smena__btn--7:checked ~ .smena__list .smena__label--7,
.smena__btn--8:checked ~ .smena__list .smena__label--8,
.smena__btn--9:checked ~ .smena__list .smena__label--9,
.smena__btn--10:checked ~ .smena__list .smena__label--10,
.smena__btn--11:checked ~ .smena__list .smena__label--11,
.smena__btn--12:checked ~ .smena__list .smena__label--12,
.smena__btn--13:checked ~ .smena__list .smena__label--13,
.smena__btn--14:checked ~ .smena__list .smena__label--14,
.smena__btn--15:checked ~ .smena__list .smena__label--15,
.smena__btn--16:checked ~ .smena__list .smena__label--16,
.smena__btn--17:checked ~ .smena__list .smena__label--17,
.smena__btn--18:checked ~ .smena__list .smena__label--18,
.smena__btn--19:checked ~ .smena__list .smena__label--19 {
  color: var(--white);
  background: var(--gr-4);
  transition: 500ms ease;
}
.smena__table {
  display: flex;
  flex-wrap: wrap;
  padding: 0 20px;
}
.smena__content-title {
  width: 100%;
  outline: 2px solid var(--black);
  outline-offset: -3px;
  font-size: 44px;
  color: var(--blue);
  padding: 15px;
  text-align: center;
  font-weight: bold;
  text-shadow: 1px 1px 0 var(--black), -1px -1px 0 var(--black), -1px 1px 0 var(--black), 1px -1px 0 var(--black);
}
.smena__day {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  font-size: 24px;
  width: calc(100% / 6);
  counter-reset: section;
}
.smena--two .smena__day {
  width: calc(100% / 5);
}
.smena__content:nth-child(1) {
  padding: 25px;
  font-size: 36px;
  background-color: var(--gr-4);
  color: var(--white);
  text-align: center;
}
.smena__content:nth-child(1)::before {
  content: "";
}
.smena__content {
  padding: 15px;
}
.smena__content-count {
  opacity: 1;
}
.smena__content-count::before {
  font-weight: bold;
  counter-increment: section;
  content: counter(section) ". ";
}
.smena__item {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  display: none;
  transition: 1500ms ease;
}
.smena__btn--1:checked ~ .smena__lessons .smena__item--1,
.smena__btn--2:checked ~ .smena__lessons .smena__item--2,
.smena__btn--3:checked ~ .smena__lessons .smena__item--3,
.smena__btn--4:checked ~ .smena__lessons .smena__item--4,
.smena__btn--5:checked ~ .smena__lessons .smena__item--5,
.smena__btn--6:checked ~ .smena__lessons .smena__item--6,
.smena__btn--7:checked ~ .smena__lessons .smena__item--7,
.smena__btn--8:checked ~ .smena__lessons .smena__item--8,
.smena__btn--9:checked ~ .smena__lessons .smena__item--9,
.smena__btn--10:checked ~ .smena__lessons .smena__item--10,
.smena__btn--11:checked ~ .smena__lessons .smena__item--11,
.smena__btn--12:checked ~ .smena__lessons .smena__item--12,
.smena__btn--13:checked ~ .smena__lessons .smena__item--13,
.smena__btn--14:checked ~ .smena__lessons .smena__item--14,
.smena__btn--15:checked ~ .smena__lessons .smena__item--15,
.smena__btn--16:checked ~ .smena__lessons .smena__item--16,
.smena__btn--17:checked ~ .smena__lessons .smena__item--17,
.smena__btn--18:checked ~ .smena__lessons .smena__item--18,
.smena__btn--19:checked ~ .smena__lessons .smena__item--19 {
  display: flex;
  transition: 1500ms ease;
}
@keyframes scaleBtn {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}
.smena__btn--close {
  outline: 0;
  display: flex;
  width: 80px;
  height: 80px;
  background-color: transparent;
  font-size: 0;
  border: 0;
  position: relative;
  left: 47.8%;
  transform: translateX(50%);
  bottom: 5px;
  animation: scaleBtn 5000ms ease infinite;
}
.smena__btn--close::after,
.smena__btn--close::before {
  top: 25px;
  left: 0;
  content: "";
  display: block;
  background-color: var(--gr-3);
  width: 80px;
  height: 15px;
  border-radius: 10px;
