.swap {
  opacity: 1;
}

.swap__title {
  margin: 0;
  padding: 0;
  font-style: italic;
  font-weight: 400;
  text-align: center;
}

.swap__text {
  margin: 0;
  padding: 0;
  font-weight: 600;
  text-align: center;
}

.swap-table {
  border-collapse: collapse;
  font-size: 26px;
}

.swap-table__cell {
  width: 2%;
  border: 3px solid var(--black);
  text-align: center;
  padding: 5px;
}

.swap__row--1 .swap-table__cell {
  font-weight: 500;
  border: 3px solid var(--black);
}

.swap-table__cell--1 {
  font-weight: 500;
  border: 3px solid var(--black);
}

.swap .main {
  box-sizing: border-box;
  padding: 25px;
}


.swap__ps {
  font-size: 24px;
  font-weight: 500;
  text-align: center;
}

.swap .nav-menu {
  // display: none;
  width: auto;
  order: -1;
  padding-left: 0;
}

