.time {
  opacity: 1;
  display: grid;

  // grid-template-columns: 1fr 1fr;
  // grid-template-rows: 1fr 1fr;
  grid-column-gap: 5px;
  grid-template-areas:
    "a a a"
    "d e f";
  font-size: 45px;

  @media @desktop {
    grid-template-areas: "a a a d e f";
    column-gap: 10px;
  }

  &--position {
    // position: absolute;
    // top: 0;
    // right: 0;
    // margin-right: 10%;
    // margin-top: 0.5%;
    @media @desktop {
      // display: grid;
      // grid-columns: 1/-1;
      grid-column: 2/3;
      grid-row: 3/4;
    }
  }

  &__now {
    grid-area: a;
    text-align: center;

    font-weight: bold;
    color: var(--white);
    text-shadow:
      1px 1px 0 var(--black),
      1px -1px 0 var(--black),
      -1px -1px 0 var(--black),
      -1px 1px 0 var(--black);
    letter-spacing: 2px;

    @media @desktop {
      margin-right: 15px;
    }
  }

  &__hours {
    grid-area: a;

    &::after {
      content: ":";
      animation-name: timeIn;
      animation-duration: 2s;
      animation-timing-function: linear;
      animation-iteration-count: infinite;

    }
  }

  &__minutes {
    grid-area: b;

    &::after {
      content: ":";
    }
  }

  &__seconds {
    grid-column: c;
  }

  &__day {
    grid-area: d;
    color: var(--white);
    font-weight: bold;
    text-shadow:
      1px 1px 0 var(--black),
      1px -1px 0 var(--black),
      -1px -1px 0 var(--black),
      -1px 1px 0 var(--black);
    letter-spacing: 2px;
  }

  &__month {
    grid-area: e;
    color: var(--white);
    font-weight: bold;
    text-shadow:
      1px 1px 0 var(--black),
      1px -1px 0 var(--black),
      -1px -1px 0 var(--black),
      -1px 1px 0 var(--black);
    letter-spacing: 2px;
  }

  &__year {
    grid-area: f;
    color: var(--white);
    font-weight: bold;
    text-shadow:
      1px 1px 0 var(--black),
      1px -1px 0 var(--black),
      -1px -1px 0 var(--black),
      -1px 1px 0 var(--black);
    letter-spacing: 2px;
  }
}


@keyframes timeIn {
  0% {
    color: transparent;
  }

  50% {
    color: var(--white);
  }

  100% {
    color: transparent;
  }
}
