.nav-menu {
  opacity: 1;
  width: 100%;
  padding-left: 150px;

  &__list {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    width: 100%;
    // justify-content: center;
    column-gap: 20px;
    // margin-bottom: -1px;
  }

  &__item {
    margin: 0;
    text-transform: none;
    font-size: 28px;
    padding: 5px 10px;
    font-weight: 600;
    color: var(--white);
    cursor: pointer;
    min-height: 62px;
    text-align: center;
    border: none;
    background-size: 300% 100%;
    text-shadow: 0 1px 0 #312c2c;
    transition: all 400ms ease-in-out;
    border-radius: 5px;
    background-image: linear-gradient(to right, var(--gr-1), var(--gr-2), var(--gr-3), var(--gr-4));
    // box-shadow: 0 4px 15px 0 var(--gr-5);

    display: flex;
    align-items: center;
    justify-content: center;

    &:hover {
      box-shadow: 0 0 0 0 var(--sp-white);
      // transform: scale(0.93);
      background-position: 100% 0;
      transition: all 400ms ease-in-out;
    }

    &:focus {
      outline: none;
    }

    &:nth-last-child(1) {
      // margin-right: 0;
    }
  }

  &__label {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    column-gap: 5px;
  }
}
