.slides,
.slides2 {
  font-size: 32px;
  position: relative;
  width: 100%;


  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  box-sizing: border-box;
}

.slide2,
.slide {
  position: absolute;
  opacity: 0;
  padding: 0;
  margin: 0;
  width: 100%;
  min-height: 100%;
  height: 770px;
  // transition: 300ms ease;
}

.first,
.first2 {
  min-height: 100%;
  opacity: 1;
  transition: 300ms ease;
  background-color: transparent;
  position: relative;
}

@keyframes scale {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.05);
  }

  100% {
    transform: scale(1);
  }
}

.showing2,
.showing {
  opacity: 1;
  position: relative;
  transition: 1500ms ease;
}

.slide2,
.slide {
  transition: 1500ms ease;
  background-repeat: no-repeat;
  background-position: 50% 50%;
}

.slide-1 {
  background-color: yellow;
}

.slide-2 {
  background-color: green;
}

.slide-3 {
  background-color: tomato;
}

.slide-4 {
  background-color: darkorange;
}

.slider-5 {
  background-color: blue;
}
